.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* CUSTOM CSS */
.btn-react {
  background: linear-gradient(to right, #114f8d 0%, #945bff 51%, #114f8d 100%);
  background-size: 200% auto;
  padding: 0 38px;
  line-height: 50px;
  font-size: 14px;
  font-weight: 700;
  transition: 0.4s;
  border-radius: 0.25rem;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  cursor: pointer;
  text-transform: uppercase;
  color: #fff !important;
}

a.btn-react:hover {
  text-decoration: none;
}

.btn-react:hover {
  background-position: right;
}

/* Desktop */
@media only screen and (min-width: 992px) {
  /* navbar */
  .navbar .navbar-nav {
    margin-left: 250px;
  }

  .navbar .nav-link {
    margin-right: 40px;
    color: #173049 !important;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
  }

  /* Jumbotron */
  .jumbotron {
    background-image: url("./image/jb3.jpg");
    /* agar background dapat menutupi seluruh jumbotron */
    background-size: cover;
    position: relative;
    height: 640px;
  }

  /* about */
  .about {
    margin-top: 100px;
  }

  .about .text-about {
    text-align: center;
    margin-top: 90px;
    margin-left: 20px;
  }

  /* service */
  .service-header {
    text-align: center;
  }

  /* testimonial */
  .testimonial {
    height: 620px;
  }

  .testimonial .testi-client img {
    width: 50%;
  }

  /* blog */
  .blog .header {
    text-align: center;
  }

  /* footer */
  .skill img {
    width: 10%;
  }
}

