/* Mobile */
@media only screen and (max-width: 768px) {
  /* navbar */
  .navbar .navbar-nav {
    margin-left: 0px;
  }

  .navbar .nav-link {
    color: #173049 !important;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
  }

  /* jumbotron */
  .jumbotron {
    background-image: url("./image/jb4.jpg");
    /* agar background dapat menutupi seluruh jumbotron */
    background-size: cover;
    position: relative;
    height: 640px;
  }

  /* about */
  .about {
    margin-top: -320px;
  }

  /* service */
  .service-content .col-md-4 {
    flex: 0 0 100%;
    max-width: 100%;
  }

  /* testimonial */
  .testimonial {
    height: 580px;
  }

  .testimonial .testi-client img {
    width: 30%;
  }

  .testimonial h3 {
    font-size: 52px !important;
  }

  /* footer */
  .skill img {
    width: 25%;
  }

  .skill img {
    margin-bottom: 20px;
  }

  footer h3.brand {
    margin-top: 100px !important;
  }

  footer h3 {
    margin-top: 20px !important;
  }
}

/* NavBar */
.navbar {
  background-color: white;
  box-shadow: 0px 3px 16px 0px rgba(0, 0, 0, 0.1);
}

.navbar .nav-link:hover {
  color: #114f8d !important;
}

.navbar-brand {
  font-family: Teko;
  font-size: 32px !important;
  color: #173049 !important;
  text-transform: uppercase;
}

/* jumbotron */
.jumbotron .display-jumbo {
  margin-top: 100px;
}

.jumbotron h1 {
  color: #173049;
  font-weight: 700;
}

.jumbotron p {
  font-size: 24px;
  font-weight: 300;
  color: #173049;
}

/* info-panel */
.info-panel {
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
  background-color: white;
  margin-top: -180px;
  padding: 30px;
}

.info-panel img {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  margin-bottom: 20px;
}

.info-panel .desc {
  margin-top: -30px;
}

.info-panel h4 {
  font-size: 32px;
  text-transform: uppercase;
  font-weight: bold;
  margin-top: 5px;
  color: #173049;
}

.info-panel p {
  font-size: 18px;
  color: #6b6b6b;
  margin-top: -5px;
  font-weight: 200;
}

/* about */
.about h5 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 650;
}

.about h3 {
  color: #173049;
  font-weight: 700;
  font-size: 32px;
  text-transform: uppercase;
  font-family: teko;
}

.about p {
  color: #6b6b6b;
  font-weight: 200;
}

/* service */
.service-header {
  margin-top: 100px;
}

.service-header h5 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 650;
}

.service-header h3 {
  color: #173049;
  font-weight: 700;
  font-size: 32px;
  text-transform: uppercase;
  font-family: teko;
}

.item-service:hover {
  background-color: #173049;
  color: #ffff;
}

.item-service {
  text-align: center;
  margin: 5px;
  padding: 50px;
  transition: 0.4s;
}

.item-service h5 {
  margin-top: 30px;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}

.item-service p {
  margin-top: 20px;
  margin-bottom: 30px;
  font-size: 14px;
  font-weight: 200;
}

.item-service a {
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
}

.item-service a:hover {
  text-decoration: none;
  color: #ffff;
}

.item-service a::before {
  content: "";
  position: absolute;
  bottom: 55px;
  width: 50px;
  height: 2px;
  background: linear-gradient(90deg, #003cff 40%, #ff0000 100%);
}

/* testimonial */
.testimonial {
  margin-top: 100px;
  background-color: #173049;
}

.testimonial h3 {
  font-weight: 700;
  font-size: 64px;
  text-transform: uppercase;
  font-family: teko;
  margin: 30px 0;
}

.testimonial .testi-client {
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  margin-top: 30px;
}

.testimonial .testi-client h6 {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 800;
}

.testimonial .testi-client i {
  font-size: 18px;
  font-weight: 100;
}

.testimonial .num-page {
  width: 20px;
  background: #fff;
  border-radius: 100%;
  border-bottom: 10px solid transparent;
  transition: 0.6s;
}

/* Project */
.project {
  margin-top: 100px;
}

.project .header h5 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 650;
}

.project .header h3 {
  color: #173049;
  font-weight: 700;
  font-size: 32px;
  text-transform: uppercase;
  font-family: teko;
}

.project .body .my-project {
  position: relative;
  margin-bottom: 30px;
}

.project .body .my-project .overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: 0.4s;
}

.project .body .my-project .info {
  position: absolute;
  bottom: 44px;
  left: 80px;
  color: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease 0s;
}

.project .body .my-project .info h4 {
  text-transform: capitalize;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 5px;
}

.project .body .my-project .info p {
  font-size: 14px;
}

.project .body .my-project:hover .overlay {
  top: 20px;
  left: 20px;
  background: rgba(19, 69, 230, 0.9);
}

.project .body .my-project:hover .info {
  visibility: visible;
  opacity: 100;
}

/* blog */
.blog {
  margin-top: 100px;
}

.blog .header h5 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 650;
}

.blog .header h3 {
  color: #173049;
  font-weight: 700;
  font-size: 32px;
  text-transform: uppercase;
  font-family: teko;
}

.blog .card-title a {
  color: #173049;
  font-weight: 700;
  font-size: 18px;
}

.blog .card-title a:hover {
  text-decoration: none;
  color: #114f8d;
}

.blog .card-text {
  font-weight: 100;
  color: #6b6b6b;
  margin-top: 20px;
  margin-bottom: 30px;
}

.blog .card-link {
  font-size: 14px;
  font-weight: 100;
  color: #6b6b6b;
}

.blog .card-link:hover {
  color: #114f8d;
}

/* footer */
